.global-top-bar-seller{

    height: 60px;
    background: #232a31;
    font-size: 14px;
    a{
        color:#ffffff;
        &:hover{
            color:#ffffff;
            text-decoration: none;
        }
    }
    
    .logo{
        padding-top: 11px;
        a{
            display: block;
            width: 161px;
            height: 40px;
            background: url(../../images/seller-logo-160x40.png);
        }
    }

    .nav{
        margin-left: 40px;
        float: left;
        display: inline;
        .item{
            position: relative;
            float:left;
            display:inline; 
            padding: 20px 25px 19px 25px;
            &:hover{
                background: #2b333c;
                cursor: pointer;
                .ui-triangle{
                    /* 以下是旋转 */
                    -moz-transform:rotate(180deg);/* firefox */
                    -moz-transform-origin:50% 50%;
                    
                    -webkit-transform:rotate(180deg);/* chrome & safari */
                    -webkit-transform-origin:50% 50%;
                    
                    -o-transform:rotate(180deg);/* Opera */
                    -o-transform-origin:50% 50%;
                    
                    transform:rotate(180deg);/* ie */
                    transform-origin:50% 50%;
                    filter:progid:DXImageTransform.Microsoft.BasicImage(rotation=2);

                }
            }
            .ui-triangle{
                left: auto;
                right: 16px;
                top: 28px;

        
            }

            .txt{
                padding-right:5px;
            }
        }

        
        .dropdown-menu{

            display:none;
            position: absolute;
            top: 60px;
            left: 0px;
            background: #fff;
            padding: 10px 0 10px 0;
            li{
                width:140px;
                padding: 10px 20px 10px 20px;
                &:hover{
                    background: #f5f5f5;
                }

            }
        }


        .dropdown-menu-hover{

            .ui-triangle{
                border-width:0px 4px 4px 4px;/* 大小 */
                border-color: transparent transparent #000 transparent;/* 颜色 */
                _border-color: #000 #000 #fff #fff;/* 颜色 */
                _filter:chroma(color=white);
            }
            .dropdown-menu{
                display: block;
               
            }
            background: #fff;
            color:#000;
            a{
                color:#000;
            }
            &:hover{
                background: #fff;
                color:#000;
            }
        }

    }

    .seller-info{
        float:right;
        display:inline; 
        .dropdown-menu{
            li{
                width:85px;
            }
        } 
    }


}









